import React from "react";
import { Box, Typography } from '@mui/material';

const InfoPrivacy = () => {
    return(
        <Box sx={stackStyle}>
            <Typography sx={stackTy1}>Aviso de privacidad</Typography>
            <Typography sx={stackTy2}>
            Ultima actualizacin: 17-03-2023 
            <br/><br/>
            En Comparo Land, tratamos de recolectar la menor cantidad posible de informacin personal de nuestros usuarios. Sin embargo, es necesario proporcionar una direccin de correo electrnico para crear una cuenta, recibir alertas de precios y otros mensajes relacionados con la pgina. Si prefieres no ser contactado por nosotros, por favor no proporciones tu correo electrnico en nuestra pgina. Si en algn momento deseas dejar de recibir correos o ser eliminado de nuestra base de datos, puedes solicitarlo enviando un correo a soporte@comparo.land.
            <br/><br/>
            Para financiar la pgina, utilizamos servicios de terceros, incluyendo marketing de afiliados. En algunos casos, si compras un producto a travs de nuestra pgina, podemos recibir una comisin por esa venta. Sin embargo, esto no afectar el precio final que pagars por el producto, ya que estamos comprometidos a ofrecerte el mejor precio posible. No recopilamos informacin sobre tus compras ni almacenamos ningn dato relacionado con tu informacin financiera.
            <br/><br/>
            Si tienes alguna otra pregunta sobre nuestra poltica de privacidad, no dudes en ponerte en contacto con nosotros en cualquier momento.
            </Typography>
        </Box>
    )
}

export default InfoPrivacy;

const stackStyle = {
    width: {
        xs: '18em',
        sm: '22.5em',
        md: '25em',
        lg: '35em',
        xl: '40em'
    },
    marginTop: '2em',
    marginBottom: '2em'
}

const stackTy1 = {
    fontSize: '1.5em',
}

const stackTy2 = {
    marginTop: '10px',
    textAlign: 'justify',
    fontSize: {
        xs: '14px',
        sm: '24px'
    }
}